@import './_reset.scss';

// 변수 만들 때 $변수명 으로 만들면 됨
$메인컬러 : #ff0000;

.red {
    color : $메인컬러;
}

// 함수를 만들 때는 @mixin
@mixin 함수() {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

// 함수를 불러올 때는 @include
.my-alert {
    @include 함수()
}

.my-alert-yellow {
    // .my-alert의 모든 속성을 이곳에 복붙
    @extend .my-alert;
    background-color: #ffe591;
}

.my-alert p {
    margin-bottom: 0;
}

